ransition: 300ms;\r\n    transition: 300ms;\r\n}\r\n\r\nh1, h2, h3, h4, h5, h6 {\r\n  font-family: 'Roboto', sans-serif;\r\n}\r\n\r\n.btn:hover,\r\n.btn:focus{\r\n  outline: none;\r\n  box-shadow: none;\r\n  cursor: pointer !important;\r\n}\r\n\r\n.navbar-toggle {\r\n  background-color: #000;\r\n}\r\n\r\na#scrollUp {\r\n  bottom: 0px;\r\n  right: 10px;\r\n  padding: 5px 10px;\r\n  background: #FE980F;\r\n  color: #FFF;\r\n  -webkit-animation: bounce 2s ease infinite;\r\n  animation: bounce 2s ease infinite;\r\n}\r\n\r\na#scrollUp i{\r\n  font-size: 30px;\r\n}\r\n\r\n\r\n/*************************\r\n*******Header CSS******\r\n**************************/\r\n\r\n.header_top {\r\n  background: none repeat scroll 0 0 #F0F0E9;\r\n}\r\n\r\n.contactinfo ul li:first-child{\r\n    margin-left: -15px;\r\n}\r\n\r\n.contactinfo ul li a{\r\n  font-size: 12px;\r\n  color: #696763;\r\n  font-family: 'Roboto', sans-serif;\r\n}\r\n\r\n\r\n.contactinfo ul li a:hover{\r\n\tbackground:inherit;\r\n}\r\n\r\n\r\n.social-icons ul li a {\r\n  border: 0 none;\r\n  border-radius: 0;\r\n  color: #696763;\r\n  padding:0px;\r\n}\r\n\r\n\r\n.social-icons ul li{\r\n\tdisplay:inline-block;\r\n}\r\n\r\n.social-icons ul li a i {\r\n  padding: 11px 15px;\r\n   transition: all 0.9s ease 0s;\r\n  -moz-transition: all 0.9s ease 0s;\r\n  -webkit-transition: all 0.9s ease 0s;\r\n  -o-transition: all 0.9s ease 0s;\r\n}\r\n\r\n.social-icons ul li a i:hover{\r\n  color: #fff;\r\n   transition: all 0.9s ease 0s;\r\n  -moz-transition: all 0.9s ease 0s;\r\n  -webkit-transition: all 0.9s ease 0s;\r\n  -o-transition: all 0.9s ease 0s;\r\n}\r\n\r\n\r\n.fa-facebook:hover {\r\n  background: #0083C9;\r\n}\r\n\r\n.fa-twitter:hover  {\r\n\tbackground:#5BBCEC;\r\n}\r\n\r\n.fa-linkedin:hover  {\r\n\tbackground:#FF4518;\r\n}\r\n\r\n.fa-dribbble:hover  {\r\n\tbackground:#90C9DC;\r\n}\r\n\r\n.fa-google-plus:hover  {\r\n\tbackground:#CE3C2D;\r\n}\r\n\r\n.header-middle .container .row {\r\n  border-bottom: 1px solid #f5f5f5;\r\n  margin-left: 0;\r\n  margin-right: 0;\r\n  padding-bottom: 0px;\r\n  padding-top: 20px;\r\n}\r\n\r\n.header-middle .container .row .col-sm-4{\r\n  padding-left: 0;\r\n}\r\n\r\n.header-middle .container .row .col-sm-8 {\r\n\tpadding-right:0;\r\n}\r\n\r\n.usa {\r\n  border-radius: 0;\r\n  color: #B4B1AB;\r\n  font-size: 12px;\r\n  margin-right: 20px;\r\n  padding: 2px 15px;\r\n  margin-top: 10px;\r\n}\r\n\r\n.usa:hover {\r\n\tbackground:#FE980F;\r\n\tcolor:#fff;\r\n\tborder-color:#FE980F;\r\n}\r\n\r\n.usa:active, .usa.active {\r\n  background: none repeat scroll 0 0 #FE980F;\r\n  box-shadow: inherit;\r\n  outline: 0 none;\r\n}\r\n\r\n.btn-group.open .dropdown-toggle {\r\n  background: rgba(0, 0, 0, 0);\r\n  box-shadow: none;\r\n}\r\n\r\n.dropdown-menu  li  a:hover, .dropdown-menu  li  a:focus {\r\n  background-color: #FE980F;\r\n  color: #FFFFFF;\r\n  font-family: 'Roboto', sans-serif;\r\n  text-decoration: none;\r\n}\r\n\r\n\r\n.shop-menu ul li {\r\n\tdisplay:inline-block;\r\n  padding-left: 15px;\r\n  padding-right: 15px;\r\n}\r\n\r\n.shop-menu ul li:last-child {\r\n  padding-right: 0;\r\n}\r\n\r\n\r\n.shop-menu ul li a {\r\n  background: #FFFFFF;\r\n  color: #696763;\r\n  font-family: 'Roboto', sans-serif;\r\n  font-size: 14px;\r\n  font-weight: 300;\r\n  padding:0;\r\n  padding-right: 0;\r\n  margin-top: 10px;\r\n}\r\n\r\n\r\n.shop-menu ul li a i{\r\n\tmargin-right:3px;\r\n}\r\n\r\n\r\n.shop-menu ul li a:hover {\r\n\tcolor:#fe980f;\r\n\tbackground:#fff;\r\n}\r\n\r\n\r\n.header-bottom {\r\n  padding-bottom: 30px;\r\n  padding-top: 30px;\r\n}\r\n\r\n.navbar-collapse.collapse{\r\n  padding-left: 0;\r\n}\r\n\r\n.mainmenu ul li{\r\n  padding-right: 15px;\r\n  padding-left: 15px;\r\n}\r\n\r\n.mainmenu ul li:first-child{\r\n  padding-left: 0px;\r\n}\r\n\r\n.mainmenu ul li a {\r\n\tcolor: #696763;\r\n\tfont-family: 'Roboto', sans-serif;\r\n\tfont-size: 17px;\r\n\tfont-weight: 300;\r\n\tpadding: 0;\r\n\tpadding-bottom: 10px;\r\n}\r\n\r\n.mainmenu ul li a:hover, .mainmenu ul li a.active,  .shop-menu ul li a.active{\r\n\tbackground:none;\r\n\tcolor:#fdb45e;\r\n}\r\n\r\n.search_box input {\r\n  background: #F0F0E9;\r\n  border: medium none;\r\n  color: #B2B2B2;\r\n  font-family: 'roboto';\r\n  font-size: 12px;\r\n  font-weight: 300;\r\n  height: 35px;\r\n  outline: medium none;\r\n  padding-left: 10px;\r\n  width: 155px;\r\n  background-repeat: no-repeat;\r\n  background-position: 130px;\r\n}\r\n\r\n\r\n/*  Dropdown menu*/\r\n\r\n.navbar-header\r\n.navbar-toggle .icon-bar {\r\n    background-color: #fff;\r\n}\r\n\r\n\r\n.nav.navbar-nav > li:hover > ul.sub-menu{\r\n  display: block;\r\n  -webkit-animation: fadeInUp 400ms;\r\n  -moz-animation: fadeInUp 400ms;\r\n  -ms-animation: fadeInUp 400ms;\r\n  -o-animation: fadeInUp 400ms;\r\n  animation: fadeInUp 400ms;\r\n}\r\n\r\nul.sub-menu {\r\n\tposition: absolute;\r\n\ttop: 30px;\r\n\tleft: 0;\r\n\tbackground: rgba(0, 0, 0, 0.6);\r\n\tlist-style: none;\r\n\tpadding: 0;\r\n\tmargin: 0;\r\n\twidth: 220px;\r\n\t-webkit-box-shadow: 0 3px 3px rgba(0, 0, 0, 0.1);\r\n\tbox-shadow: 0 3px 3px rgba(0, 0, 0, 0.1);\r\n\tdisplay: none;\r\n\tz-index: 999;\r\n}\r\n\r\n.dropdown ul.sub-menu li .active{\r\n  color: #FDB45E;\r\n  padding-left: 0;\r\n}\r\n\r\n\r\n.navbar-nav li ul.sub-menu li{\r\n  padding: 10px 20px 0 ;\r\n}\r\n\r\n.navbar-nav li ul.sub-menu li:last-child{\r\n  padding-bottom: 20px;\r\n}\r\n\r\n.navbar-nav li ul.sub-menu li a{\r\n  color: #fff;\r\n}\r\n\r\n.navbar-nav li ul.sub-menu li a:hover{\r\n    color: #FDB45E;\r\n}\r\n\r\n.fa-angle-down{\r\n  padding-left: 5px;\r\n}\r\n\r\n@-webkit-keyframes fadeInUp {\r\n  0% {\r\n    opacity: 0;\r\n    -webkit-transform: translateY(20px);\r\n    transform: translateY(20px);\r\n  }\r\n\r\n  100% {\r\n    opacity: 1;\r\n    -webkit-transform: translateY(0);\r\n    transform: translateY(0);\r\n  }\r\n}\r\n\r\n/*************************\r\n*******Footer CSS******\r\n**************************/\r\n\r\n#footer {\r\n  background: #F0F0E9;\r\n}\r\n\r\n\r\